import React from 'react';
import { css } from '@emotion/react';
import { useLocation } from '@reach/router';
import { Link } from 'gatsby';

type Props = {
  totalCount: number;
};

function CreatePageNumber(start: number, end: number): Array<number> {
  const PageArray = [];
  for (let i = start; i <= end; i++) {
    PageArray.push(i);
  }
  return PageArray;
}

function CurrentPage(pathname: string): number {
  if (pathname === '/') {
    return 1;
  } else {
    if (parseInt(pathname.split('/')[2])) {
      return parseInt(pathname.split('/')[2]);
    } else {
      return 1;
    }
  }
}

export default function Pagenation({ totalCount }: Props) {
  const location = useLocation(); // url Ï†ïÎ≥¥Î•º Í∞ÄÏ†∏Ïò§Í∏∞ ÏúÑÌï¥

  const limit = 5; // ÌéòÏù¥ÏßÄÎãπ 5Í∞úÏî©
  const count = 5; // ÌéòÏù¥ÏßÄÎÑ§Ïù¥ÏÖò Î≤àÌò∏Í∞Ä 5Í∞úÏî© ÎÅäÏñ¥ÏÑú Î≥¥Ïó¨Ï£ºÍ∏∞

  const page = CurrentPage(location.pathname);
  const totalPage = Math.ceil(totalCount / limit); // Ï¥ù ÌéòÏù¥ÏßÄ Ïàò

  // 5Í∞úÏî© ÌéòÏù¥ÏßÄÎÑ§Ïù¥ÏÖò Ìï† ÏòàÏ†ï
  // ÏòàÎ•º Îì§Ïñ¥ 3Ïù¥Î©¥ 1~5ÍπåÏßÄ. Í∑ºÎç∞ totalPageÍ∞Ä 4ÎùºÎ©¥ 1~4ÍπåÏßÄ
  const start = (Math.ceil(page / count) - 1) * count + 1;
  const end = start + (count - 1) > totalPage ? totalPage : start + (count - 1);

  return (
    <div css={PagenationWrapper}>
      <ul>
        {start !== 1 ? <Link to={`/pages/${start - 1}`}>üëà</Link> : null}
        {CreatePageNumber(start, end).map((v, idx) =>
          v === 1 ? (
            <li key={idx}>
              <Link to="/" activeClassName="active">
                {v}
              </Link>
            </li>
          ) : (
            <li key={idx}>
              <Link to={`/pages/${v}`} activeClassName="active">
                {v}
              </Link>
            </li>
          )
        )}
        {start + (count - 1) < totalPage ? (
          <Link to={`/pages/${end + 1}`}>üëâ</Link>
        ) : null}
      </ul>
    </div>
  );
}

const PagenationWrapper = css`
  margin: 2rem 0 4rem;

  ul {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    align-items: center;
    justify-content: center;
  }

  li {
    background: #81d4fa;
    border-radius: 50px;
    width: 3.3rem;
    height: 3.3rem;
    text-align: center;
    color: white;
    margin: 0 1rem;

    cursor: pointer;
  }

  a {
    font-size: 2rem;
    padding: 0.4rem 1.2rem;
  }

  a.active {
    background: #03a9f4;
    border-radius: 50px;
  }

  img {
    padding: 0 1rem;
  }
`;
